<template>
  <div class="product-wrapper">
    <div class="list ">
      <div class='pName'>{{productInfo.name}}</div>
      <!--<div class="pMsg">-->
      <!--<span class="condition-item" v-for="(item,index) in productInfo.insure_condition" :key="index">{{item.name}}：{{item.value}}</span>-->
      <!--</div>-->
      <div class="marks ">
          <span class="" v-if="productInfo.feature1"><i></i>{{productInfo.feature1}}</span>
          <span class="" v-if="productInfo.feature2"><i></i>{{productInfo.feature2}}</span>
          <span class="" v-if="productInfo.feature3"><i></i>{{productInfo.feature3}}</span>
      </div>
      <div class="characteristics">
        <div class="character " v-for="(item,index) in parameters.entries" :key="index">
          <span>{{item.name}}</span>
          <span>{{item.limit}}</span>
        </div>
      </div>
      <div class="logo"><img :src="baseSrc+productInfo.brandLogo" alt="公司logo"></div>
      <div class="price-btn">
        <div class="price">{{productInfo.price}}<span >元起</span></div>
        <div class="btn" @click="goDetail(productInfo.id)">查看详情</div>
      </div>
        <slot></slot>
    </div>

  </div>
</template>

<script>
export default {
  props: {
    leftWidth: {
      type: Number
    },
    rightWidth: {
      type: Number,
      default: 260
    },
    productInfo: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  data () {
    return {
      baseSrc: '',
      parameters: []
    }
  },
  mounted () {
    this.baseSrc = `${process.env.VUE_APP_RESOURCE_URL}`
    if (this.productInfo.parameter_values === null) return
    this.parameters = this.productInfo.parameter_values[0]
  },
  computed: {
    comLeftWidth () {
      return {
        width: this.leftWidth ? this.leftWidth + 'px' : '800px'
      }
    },
    comRightWidth () {
      return {
        width: this.rightWidth ? this.rightWidth + 'px' : '800px'
      }
    }
  },
  methods: {
    goDetail (id) {
      this.$router.push({
        path: `/product/detail/${id}`,
        query: {
          productId: id
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped="scoped">
  @import '@/assets/styles/mixin.scss';

  .product-wrapper {
    .list {
      margin-bottom: 12px;
      background: #FFFFFF;
      /*width: 800px;*/
      .pName {
        font-size: pxToRem(36px);
        color: $color-text;
        padding-top: pxToRem(30px);
        line-height: pxToRem(36px);
        font-weight: 600;
        padding-left: pxToRem(30px);
      }
      .pMsg {
        font-size: 14px;
        color: #B6BBBB;
        line-height: 30px;
        white-space: nowrap;
        .condition-item {
          font-size: 14px;
          color: #B6BBBB;
          &:not(:last-child):after {
            content: '|';
          }
        }
      }
      .marks {
        font-size: 0;
        padding:pxToRem(30px) 0  pxToRem(30px) pxToRem(30px);
        i {
          display: inline-block;
          vertical-align: top;
          margin-top:pxToRem(-2px);
          background: url("../../assets/images/icon_ts.png") no-repeat;
          background-size: pxToRem(28px) pxToRem(28px);
          width: pxToRem(28px);
          height: pxToRem(28px);
          margin-right: pxToRem(12px);
        }
        span {
          line-height: 16px;
          color: $color-text;
          font-size: 14px;
          margin-right: pxToRem(50px);
          &:first-child {
            padding-left: 0;
          }
        }

      }
      .characteristics {
        margin-left: pxToRem(30px);
        border-bottom:$border-bottom-light-color;
        padding-bottom:pxToRem(10px);
        .character {
          display: flex;
          padding-right: pxToRem(30px);
          justify-content: space-between;
          font-size: pxToRem(28px);
          margin-bottom:pxToRem(20px);
          color: #A5A8A8;
        }
      }
      .logo {
        position: absolute;
        right: pxToRem(30px);
        top:  pxToRem(30px);
        width: pxToRem(110px);
        height: pxToRem(60px);
        overflow: hidden;
        /*background: rgba(0, 166, 0, .6);*/
        img {
          width: pxToRem(110px);
          height: pxToRem(60px);
        }
      }
      .price-btn{
        display: flex;
        justify-content: space-between;
        height:pxToRem(100px);
        line-height: pxToRem(100px);
        align-items: center;
        padding:0 pxToRem(30px) 0 pxToRem(28px);
        .price{
          font-size: pxToRem(40px);
          color: $color-active-text;

          span{
            font-size: pxToRem(24px);
            color: #D5D8D8;
          }
        }
        .btn{
          font-size: pxToRem(24px);
          background: $color-active-text;
          border-radius: pxToRem(34px);
          height:pxToRem(60px);
          line-height: pxToRem(60px);
          width:pxToRem(150px);
          text-align: center;
          color: #fff;
        }
      }
      .part-rt {
        position: relative;
        overflow: hidden;
        float: right;
        background-color: rgba(241, 244, 244, .5);
        height: 100%;
        width: 260px;
      }
    }
  }
</style>
