<template>
  <div>
    <ul>
      <li v-for="(item,index) in productList" :key="index">
        <product :productInfo="item">
        </product>
      </li>
    </ul>
  </div>
</template>

<script>

import product from '@/components/product/BaseProduct'

export default {
  name: 'ProductList',
  data () {
    return {
      page: 1,
      limit: 10,
      productList: []
    }
  },
  mounted () {
    this.getList()
  },
  methods: {
    getList () {
      // this.noItem = false
      return new Promise((resolve, reject) => {
        this.$api.product.getProductListWidthType({
          productCategoryId: 5,
          page: this.page,
          limit: this.limit
        }).then(res => {
          this.productList = res.data
          // this.productList
        })
      })
    }
  },
  components: {
    product
  }
}
</script>

